.page-footer {
  background-color: $basic-grey;
}

.page-footer__wrapper {
  min-width: 320px;
  min-height: 160px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0 30px;
}

.page-footer__logo {
  display: none;
}

.page-footer__logo[href]:hover .page-footer__logo-image {
  fill: $another-green;
}

.page-footer__logo[href]:active .page-footer__logo-image {
  fill: $another-green;
  opacity: 0.3;
}

.page-footer__social {
  margin: 0;
}

@media (min-width: $tablet) {
  .page-footer__wrapper {
    max-width: 768px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px;
  }

  .page-footer__logo {
    display: block;
  }

  .page-footer__social {
    margin: 0;
  }

  /* .page-footer__logo-image {
    width: 102px;
    height: 34px;
  } */

  .page-footer__logo-wrapper {
    padding-bottom: 20px;
  }
}

@media (min-width: $desktop) {
  .page-footer__wrapper {
    max-width: 1150px;
  }
}
